.json-editor {
    display: flex;
    align-items: center;
    font-family: var(--mono-font);
    font-size: 0.9rem;
    margin: 1rem 0;
}    
.json-editor textarea {
    font-family: inherit;
    font-size: 0.9rem;
    width: 50%;
}
.result {
    white-space: pre;
    background-color: var(--col-prim);
    margin: 0 1rem;
    padding: 0.1rem 0.5rem;
    border-radius: 0.5rem;
    max-width: 50%;
    overflow: auto;
}
